<template>
    <div>
        <div class="accordion" id="accordion_jlpt_vocabularies_list">
            <div
                class="accordion-item"
                v-for="(item, index) in items_list"
                :key="index"
            >
                <h2 class="accordion-header" :id="`heading_${index}`">
                    <div
                        class="accordion-button collapsed"
                        type="button"
                        data-bs-toggle="collapse"
                        aria-expanded="false"
                        :data-bs-target="`#collapse_${index}`"
                        :aria-controls="`collapse_${index}`"
                    >
                        {{
                            item.word && item.word != ""
                                ? item.word
                                : item.pronunciation
                        }}
                    </div>
                </h2>
                <div
                    :id="`collapse_${index}`"
                    :aria-labelledby="`heading_${index}`"
                    data-bs-parent="#accordion_jlpt_vocabularies_list"
                    class="accordion-collapse collapse"
                >
                    <div class="accordion-body">
                        <p>Pronunciation:</p>
                        <p>
                            <b
                                ><span
                                    class="text-success"
                                    v-html="item.pronunciation"
                                ></span
                            ></b>
                        </p>
                        <p v-if="false">Type:</p>
                        <p v-if="false">
                            <b
                                ><span
                                    class="text-success"
                                    v-html="item.type"
                                ></span
                            ></b>
                        </p>
                        <p>Meaning:</p>
                        <span class="text-success" v-html="item.meaning"></span>
                    </div>
                </div>
            </div>
        </div>

        <button
            @click="moveToTop"
            class="btn btn-outline-primary rounded-circle btn-sm move-2-top-icon"
        >
            <i class="fa-solid fa-jet-fighter-up"></i>
        </button>
    </div>
</template>

<script>
import LocalStorage from "@/utils/LocalStorage.js";
import VocabulariesTopics from "@/data/japanese/index.js";

export default {
    data() {
        return {
            topics: null,
            items_list: null,
            selected_index: null,
        };
    },

    async created() {
        await this.init();
    },

    methods: {
        async init() {
            this.localStorage = new LocalStorage();
            this.localStorage.setLanguageKey("japanese-vietnamese");
            /* eslint-disable */
            switch (this.localStorage.language_key) {
                // case "japanese-english":
                //     break;
                // case "japanese-vietnamese":
                //     break;
                default:
                    this.topics = VocabulariesTopics.vietnamese;
                    break;
            }

            this.items_list =
                this.topics[this.localStorage.setting.level.topic].value[
                    this.localStorage.setting.level.level
                ].value[this.localStorage.setting.level.sub_level].value;
        },

        async handleShowTopicContent(selected_index) {
            if (this.selected_index == selected_index) {
                this.selected_index = null;
            } else {
                this.selected_index = selected_index;
            }
        },

        moveToTop() {
            window.scrollTo({ top: 0, behavior: "smooth" });
        },
    },
};
</script>
